<template>
    <div class="commute-discount">
        <h1>回數票優惠</h1>
        <div class="tab-container">
          <div class="tab" v-for="(tab, index) in tabs" :key="index" :class="{ active: activeTab === index }" @click="switchTab(index)">
            {{ tab }}
          </div>
        </div>
        <div class="img-container">
          <img :src="require('@/assets/images/image7.webp')" alt="回數優惠">
        </div>
    </div>
</template>

<script>
export default {
  name: "commuteDiscount",
  data () {
    return {
      tabs: ["定期票優惠", "回數票優惠"],
      activeTab: 1
    }
  },
  methods: {
    switchTab (index) {
      this.activeTab = index
      console.log(`Switched to tab: ${this.tabs[index]}`)
      if (index === 0) {
        this.$router.push('/periodical-discount')
      }
    }
  }
}
</script>

<style scoped>
.commute-discount {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 800px;
  margin: 0 auto;
  padding-top: 100px;
}
.commute-discount h1 {
  border-bottom: 2px solid #f60;
  font-weight: bold;
}
.tab-container {
  display: flex;
  justify-content: flex-start;
  width: 100%;
}
.tab {
  padding: 12px 20px;
  cursor: pointer;
  text-align: center;
  font-size: 16px;
  background-color: #e0e0e0;
  color: gray;
}
.tab.active {
  background-color: #d94e1f;
  color: white;
  position: relative;
}
.tab.active::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #d94e1f;
}
.img-container img {
  padding-top: 20px;
  width: 1200px;
  height: 300px;
}
@media (max-width: 768px) {
  .img-container img {
    width: 500px;
    height: 300px;
  }
}
</style>
